'use strict';

if (window.theme.template == 'product') {

    if (isSub) {
        var planHandler = function planHandler(event) {
            activeSellingPlanBtn.classList.remove('active');
            activeSellingPlanBtn = this;
            activeSellingPlanBtn.classList.add('active');
            // sellingPlan = activeSellingPlanBtn.dataset.planId;
            frequency = activeSellingPlanBtn.dataset.frequency;
            unitType = activeSellingPlanBtn.dataset.unitType;
            activeVariant = findVariantID();
        };

        var radioHandler = function radioHandler(event) {
            var parent = this.parentNode;
            var activeSelector = document.querySelector('.rc_widget__option__selector.active');
            activeSelector.classList.remove('active');
            parent.classList.add('active');

            if (this.value === 'onetime') {
                submitBtn.classList.remove('sub-active');
                frequencySelectors.classList.add('hide');
            } else if (this.value === 'subsave') {
                frequencySelectors.classList.remove('hide');
                submitBtn.classList.add('sub-active');
                activeVariant = findVariantID();
                var variantID = findVariantID();
                variantID = subscriptions.original_to_hidden_variant_map[variantID.id];
                var priceSpan = $('span[data-product-price]');
                var newPrice = variantID.discount_variant_price;
                variantID = variantID.discount_variant_id;

                $(priceSpan[0]).html('$' + newPrice);
            }
        };

        var findVariantID = function findVariantID() {
            var match;
            var subSku;
            var activeSku;

            subscriptionProduct.forEach(function (item) {
                match = subscriptionProduct.filter(function (v) {
                    subSku = v.sku;
                    activeSku = activeVariant.sku;
                    return subSku === activeSku ? subSku : '';
                });
            });

            return match[0];
        };

        var variantHandler = function variantHandler() {
            var variantID;
            var newPrice;
            var subActive = submitBtn.classList.contains('sub-active');
            if (subActive) {
                variantID = findVariantID();
                variantID = subscriptions.original_to_hidden_variant_map[variantID.id];

                newPrice = variantID.discount_variant_price;
                variantID = variantID.discount_variant_id;
                var priceSpan = $('span[data-product-price]');

                $(priceSpan[0]).html('$' + newPrice);
            }
        };

        var addItemToCart = function addItemToCart(event) {

            var variant_id;
            var subActive = this.classList.contains('sub-active');
            var qty = quantity.valueAsNumber;
            var data;

            if (subActive) {
                event.preventDefault();
                variant_id = findVariantID();
                variant_id = subscriptions.original_to_hidden_variant_map[variant_id.id];
                variant_id = variant_id.discount_variant_id;

                data = {
                    "id": variant_id,
                    "quantity": qty,
                    "properties": {
                        "shipping_interval_frequency": frequency,
                        "shipping_interval_unit_type": unitType
                    }
                };

                jQuery.ajax({
                    type: 'POST',
                    url: '/cart/add.js',
                    data: data,
                    dataType: 'json',
                    success: function success() {
                        window.location.href = '/cart';
                    }
                });
                window.location = '/checkout';
            }
        };

        // function addToCart(event) {
        //     var subActive = this.classList.contains('sub-active');
        //     var data = {};
        //     var qty = quantity.valueAsNumber;


        //     if(subActive) {
        //         event.preventDefault();
        //         data = {
        //             "id": activeVariant.id,
        //             "quantity": qty,
        //             "selling_plan": sellingPlan
        //           }

        //           console.log(data);
        //           axios.post('/cart/add.js', data)
        //           .then(function (response) {
        //             window.location = '/cart';
        //           })
        //           .catch(function (req, status, error) {
        //               console.log(req, status, error);
        //               console.log(status);
        //                 console.log(error);
        //           });
        //     }
        // }


        var axios = require('axios');

        var radios = document.querySelectorAll('input[type=radio][name="subscriptionRadio"]');
        var submitBtn = document.querySelector('.product_form--add-button');

        var activeSelector = document.querySelector('.rc_widget__option__selector.active');
        var sellingPlanBtns = document.querySelectorAll('.product_sub_btn');
        var activeSellingPlanBtn = document.querySelector('.product_sub_btn.active');
        // var sellingPlan = activeSellingPlanBtn.dataset.planId;
        var frequency = activeSellingPlanBtn.dataset.frequency;
        var unitType = activeSellingPlanBtn.dataset.unitType;
        var quantity = document.getElementById('Quantity');
        var frequencySelectors = document.querySelector('.frequency-selectors');
        var variantInputs = document.querySelectorAll('.size-button__input');

        Array.prototype.forEach.call(variantInputs, function (input) {
            input.addEventListener('change', variantHandler);
        });

        Array.prototype.forEach.call(radios, function (radio) {
            radio.addEventListener('change', radioHandler);
        });

        Array.prototype.forEach.call(sellingPlanBtns, function (btn) {
            btn.addEventListener('click', planHandler);
        });

        submitBtn.addEventListener('click', addItemToCart);
    }
}